<template>
  <div class="um-demo-page">
    <div class="um-demo-hd">
      <h1 class="um-demo-title">UI Nuclear Mobile</h1>
      <h2 class="um-demo-subtitle"></h2>
    </div>
    <div class="um-demo-bd">
      <List v-for="(cat, index) in category" :key="index" :class="cat.opened ? 'category-open' : 'category-closed'" @click.native="category[index].opened = !category[index].opened" >
        <template slot="renderHeader">
          <div class="um-demo-category">
            <div class="um-demo-category-name">{{cat.name}}</div>
            <div class="um-demo-category-arrow">
              <span>
                <Icon type="down"></Icon>
              </span>
            </div>
          </div>
        </template>
        <ListItem v-for="item in cat.item" :key="item.name" arrow="horizontal" multipleLine @click.native="routeTo(item.path)">{{item.name}}</ListItem>
      </List>
    </div>
  </div>
</template>
<script>
import Button from '@/components/button'
import WhiteSpace from '@/components/white-space'
import WingBlank from '@/components/wing-blank'
import List from '@/components/list'
import ListItem from '@/components/list-item'
import Icon from '@/components/icon'
export default {
  components: {
    Button,
    List,
    ListItem,
    WhiteSpace,
    WingBlank,
    Icon
  },
  data () {
    return {
      category:
      [
        {
          name: ' Layout',
          opened: false,
          item: [
            {
              name: 'Flex Flex',
              path: 'flex'
            },
            {
              name: 'WhiteSpace ',
              path: 'white-space'
            },
            {
              name: 'WingBlank ',
              path: 'wing-blank'
            }
          ]
        },
        {
          name: ' Navigation',
          opened: false,
          item: [
            {
              name: 'Drawer -',
              path: 'drawer'
            },
            {
              name: 'Drawer -',
              path: 'drawer-inline'
            },
            {
              name: 'Menu ',
              path: 'menu'
            },
            {
              name: 'NavBar ',
              path: 'nav-bar'
            },
            {
              name: 'Pagination ',
              path: 'pagination'
            },
            {
              name: 'Popover ',
              path: 'popover'
            },
            {
              name: 'SegmentedControl ',
              path: 'segmented-control'
            },
            {
              name: 'TabBar ',
              path: 'tab-bar'
            },
            {
              name: 'Tabs ',
              path: 'tabs'
            }
          ]
        },
        {
          name: ' Data Entry',
          opened: false,
          item: [
            {
              name: 'Button ',
              path: 'button'
            },
            {
              name: 'Calendar ',
              path: 'calendar'
            },
            {
              name: 'Checkbox ',
              path: 'checkbox'
            },
            {
              name: 'DatePicker ',
              path: 'date-picker'
            },
            {
              name: 'DatePickerView ',
              path: 'date-picker-view'
            },
            {
              name: 'ImagePicker ',
              path: 'image-picker'
            },
            {
              name: 'InputItem ',
              path: 'input-item'
            },
            {
              name: 'Picker ',
              path: 'picker'
            },
            {
              name: 'PickerView ',
              path: 'picker-view'
            },
            {
              name: 'Radio ',
              path: 'radio'
            },
            {
              name: 'Range ',
              path: 'range'
            },
            {
              name: 'SearchBar ',
              path: 'search-bar'
            },
            {
              name: 'Slider ',
              path: 'slider'
            },
            {
              name: 'Stepper ',
              path: 'stepper'
            },
            {
              name: 'Switch ',
              path: 'switch'
            },
            {
              name: 'TextareaItem ',
              path: 'textarea-item'
            }
          ]
        },
        {
          name: ' Data Display',
          opened: false,
          item: [
            {
              name: 'Accordion ',
              path: 'accordion'
            },
            {
              name: 'Badge ',
              path: 'badge'
            },
            {
              name: 'Card ',
              path: 'card'
            },
            {
              name: 'Carousel ',
              path: 'carousel'
            },
            {
              name: 'Grid ',
              path: 'grid'
            },
            {
              name: 'Icon ',
              path: 'icon'
            },
            {
              name: 'List ',
              path: 'list'
            },
            {
              name: 'NoticeBar ',
              path: 'notice-bar'
            },
            {
              name: 'Steps ',
              path: 'steps'
            },
            {
              name: 'Tag ',
              path: 'tag'
            }
          ]
        },
        {
          name: ' Feedback',
          opened: false,
          item: [
            {
              name: 'ActionSheet ',
              path: 'action-sheet'
            },
            {
              name: 'ActivityIndicator ',
              path: 'activity-indicator'
            },
            {
              name: 'Modal ',
              path: 'modal'
            },
            {
              name: 'Progress ',
              path: 'progress'
            },
            {
              name: 'Toast ',
              path: 'toast'
            }
          ]
        },
        {
          name: ' Gesture',
          opened: false,
          item: [
            {
              name: 'PullToRefresh ',
              path: 'pull-to-refresh'
            },
            {
              name: 'SwipeAction ',
              path: 'swipe-action'
            }
          ]
        },
        {
          name: ' Combination',
          opened: false,
          item: [
            {
              name: 'ListView ',
              path: 'list-view-nav'
            },
            {
              name: 'Result ',
              path: 'result'
            }
          ]
        },
        {
          name: ' Other',
          opened: false,
          item: [
            {
              name: 'LocaleProvider ',
              path: 'locale-provider'
            }
          ]
        }
      ]
    }
  },
  methods: {
    routeTo (e) {
      // e = e.replace(/([a-z])([A-Z])/g, '$1-$2').toLowerCase()
      console.log(e)
      this.$router.push({ name: e })
    }
  }
}
</script>
<style>
.um-demo-page .um-demo-hd {
  position: relative;
  overflow: hidden;
}

.um-demo-page .um-demo-hd .um-demo-title,
.um-demo-page .um-demo-hd .um-demo-subtitle {
  text-align: left;
  padding-left: 16px;
}

.um-demo-page .um-demo-hd .um-demo-title {
  color: rgba(0, 0, 0, 0.75);
  font-size: 21px;
  font-weight: normal;
  margin: 27px 0 15px;
}

.um-demo-page .um-demo-hd .um-demo-subtitle {
  font-size: 13px;
  color: rgba(0, 0, 0, 0.75);
  font-weight: normal;
  margin: 0 0 27px;
}

.um-demo-page .um-demo-bd .um-list {
  margin: 0 16px 12px;
  background-color: white;
  border-radius: 2px;
}

.um-demo-page .um-demo-bd .um-list .um-list-header {
  padding: 0;
  padding-left: 24px;
  height: 70px;
  line-height: 70px;
  color: #404040;
  font-size: 16px;
}

.um-demo-page .um-demo-bd .um-list .um-list-body {
  border-top: none;
}

.um-demo-page .um-demo-bd .um-list .um-list-body::before {
  display: none;
}

.um-demo-page .um-demo-bd .um-list .um-list-item {
  padding-left: 24px;
}

.um-demo-page .um-demo-bd .um-list .um-list-item .um-list-content {
  color: #666;
  font-size: 14px;
}

.um-demo-page .um-demo-bd .um-list.category-closed .um-list-header {
  position: relative;
}

.um-demo-page .um-demo-bd .um-list.category-closed .um-list-body {
  display: none;
}

.um-demo-page .um-demo-category {
  display: flex;
}

.um-demo-page .um-demo-category-name {
  flex: 1;
}

.um-demo-page .um-demo-category-arrow {
  width: 50px;
  text-align: center;
  transform: rotate(0deg);
  transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
}

.um-demo-page .um-demo-category-arrow span {
  margin-top: 5px;
  display: inline-block;
  color: #c7c7cc;
}

.category-open .um-demo-category .um-demo-category-arrow {
  transform: rotate(-180deg);
}

</style>



